import React, { useState } from "react";
import data from "./data";
import "./index.css";

function List() {
  const [totalBdays, setTotalBdays] = useState(data.length + " birthdays left");
  const [allPeople, setallPeople] = useState(
    data.map((person) => {
      return (
        <section className="person">
          <img src={person.image} alt="" />
          <h4>
            {person.name}
            <p>{person.age} years</p>
          </h4>
        </section>
      );
    })
  );
  const handleClick = () => {
    setallPeople([]);
    setTotalBdays("0 birthdays left");
  };

  return (
    <>
      <>
        <section className="container">
          <h3>{totalBdays}</h3>
          {allPeople}
          <button className="btn btn-primary" onClick={handleClick}>
            Clear All
          </button>
        </section>
      </>
    </>
  );
}

export default List;
